section {
    display: flex;
    justify-content: center;
    align-items: center;
}

section .section-experience {
    display: flex;
    flex-direction: column;
    max-width: var(--breakpoint-lg);
    padding: 50px var(--spacing-lg);
    align-items: center;
    justify-content: center;
    width: 100%;
}

section .section-experience .section-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
    text-align: center;
}

section .section-experience .section-header .header-title {
    font-family: var(--font-family-header);
    font-size: var(--font-size-h4);
    color: var(--color-header);
    margin-top: 0;
    margin-bottom: var(--spacing-md);
}

section .section-experience .section-header .header-paragraph {
    font-family: var(--font-family-p);
    font-size: var(--font-size-md);
    color: var(--color-item-text);
    font-weight: 500;
    margin: 0;
}

section .section-experience .experience-container {
    display: flex;
    flex-direction: column;
    max-width: 900px;
	grid-gap: 24px;
}

section .section-experience .experience-date {
    font-family: var(--font-family-p);
    font-size: 12px;
    font-weight: 500;
    color: var(--color-item-text);
}

section .section-experience .experience-title {
    font-family: var(--font-family-header);
    font-size: var(--font-size-md);
}

section .section-experience .experience-desc {
    font-family: var(--font-family-p);
    font-size: 13px;
    font-weight: 500;
    color: #777;
}