/* Estilos para la sección de contacto */
section {
    display: flex;
    justify-content: center;
    align-items: center;
}
  
section .section-contact {
    display: flex;
    max-width: var(--breakpoint-lg);
    width: 100%;
    padding: 0 var(--spacing-lg);
}
  
section .section-contact .contact-container {
    width: 50%;
    background-color: antiquewhite;
}
  
section .section-contact .form-container {
    width: 50%;
    padding: var(--spacing-md);
}

section .section-contact .form-container .form-group {
    margin-bottom: var(--spacing-md);
}
  
section .section-contact .form-container .form-group label {
    font-family: var(--font-family-p);
    font-size: var(--font-size-lg);
    margin-bottom: var(--spacing-md);
    display: block;
}
  
section .section-contact .form-container .form-group input,
section .section-contact .form-container .form-group textarea {
    width: calc(100% - 20px);
    padding: var(--spacing-md);
    font-size: var(--font-size-lg);
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
}
  
section .section-contact .form-container button {
    background-color: #3498db;
    color: white;
    font-size: var(--font-size-lg);
    padding: var(--spacing-md) var(--spacing-lg);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}
  
section .section-contact .form-container button:hover {
    background-color: #2684c7;
}