section {
    display: flex;
    justify-content: center;
    align-items: center;
}

section .section-about {
    display: flex;
    flex-direction: column;
    max-width: var(--breakpoint-lg);
    width: 100%;
    padding: 50px var(--spacing-lg);
    justify-content: center;
}

section .section-about .section-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
    width: 100%;
}

section .section-about .section-header .header-title {
    font-family: var(--font-family-header);
    font-size: var(--font-size-h4);
    color: var(--color-header);
    margin-top: 0;
    margin-bottom: var(--spacing-md);
}

section .section-about .section-header .header-paragraph {
    font-family: var(--font-family-p);
    font-size: var(--font-size-md);
    color: var(--color-item-text);
    font-weight: 500;
    margin: 0;
}