section {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
}

section .section-home {
    display: flex;
    height: calc(100vh - 178px);
    max-height: 890px;
    min-height: 500px;
    max-width: var(--breakpoint-lg);
    width: 100%;
    padding: 50px var(--spacing-lg);
    justify-content: center;
    align-items: center;
    gap: 50px;
}

section .section-home .home-description {
    width: 50%;
    padding-left: 40px;
}

section .section-home .home-description .header-name {
    font-size: var(--font-size-h4);
    font-family: var(--font-family-p);
    font-weight: 600;
    margin: 0;
    color: var(--color-header);
}

section .section-home .home-description .header-title {
    font-size: var(--font-size-h1);
    font-family: var(--font-family-header);
    margin: 0;
    background: linear-gradient(90deg, #000122, #00505c);
    background-clip: border-box;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

section .section-home .home-description .header-subtitle {
    font-size: var(--font-size-h3);
    font-family: var(--font-family-header);
    font-weight: 600;
    line-height: 30px;
    margin-top: 0;
    margin-bottom: 40px;
    color: var(--color-header);
}

section .section-home .home-description .header-description {
    font-size: var(--font-size-base);
    font-family: var(--font-family-p);
    color: var(--color-item-text);
    font-weight: 600;
    line-height: 25px;
    max-width: 400px;
    margin: 0;
}

section .section-home .home-photo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

section .section-home .home-photo .my-photo {
    z-index: 1;
}

section .section-home .home-photo .blob {
    position: absolute;
    overflow: hidden;
    max-width: 380px;
    width: 100%;
    max-height: 330px;
    height: 100%;
    border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
    background-color: #000122;
    background-size: cover;
    background-position: center;
    animation: morph 3.75s linear infinite;
}

@keyframes morph {
    0%,
    100% {
        border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
    }
    33% {
        border-radius: 72% 28% 48% 48% / 28% 28% 72% 72%;
    }
    66% {
        border-radius: 100% 56% 56% 100% / 100% 100% 56% 56%;
    }
}

section .section-home .home-photo .my-photo {
    max-width: 400px;
}

@media screen and (max-width: 1024px) {
    section .section-home .home-description .header-name {
        font-size: 30px;
    }

    section .section-home .home-description .header-title {
        font-size: 70px;
    }

    section .section-home .home-description .header-subtitle {
        font-size: 40px;
    }

    section .section-home .home-description {
        width: 100%;
    }

    section .section-home .home-photo {
        width: 100%;
    }
}

@media screen and (max-width: 912px) {
    section .section-home {
        flex-direction: column-reverse;
    }
}

@media screen and (max-width: 769px) {
    section .section-home {
        padding: 50px var(--spacing-lg);
        height: calc(100vh - 160px);
        max-height: none;
    }

    section .section-home .home-description {
        width: 100%;
    }

    section .section-home .home-photo {
        width: 100%;
    }

    section .section-home .home-photo .my-photo {
        width: 80%;
    }
}

@media screen and (max-width: 480px) {
    section .section-home {
        max-width: none;
    }
    
    section .section-home .home-description {
        padding: 0;
    }

    section .section-home .home-description .header-name {
        font-size: var(--font-size-xl);
    }

    section .section-home .home-description .header-title {
        font-size: var(--font-size-h2);
    }

    section .section-home .home-description .header-subtitle {
        font-size: var(--font-size-h4);
        margin-bottom: 25px;
    }

    section .section-home .home-description .header-description {
        font-size: var(--font-size-md);
    }
}